body {
	margin: 0;
	padding: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
		"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: rgb(52, 55, 71);
	min-height: 100vh;
	overflow: hidden;
}

.stream-container {
	border: 2px solid green;
	resize: both;
	display: block;
	padding: 0.1em;
	height: 49vmin;
}

.App {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

#streams {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 0;
	margin: 0;
}

.box {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	border: 1px solid black;
	text-align: center;
	box-sizing: border-box;
	margin-bottom: 10px;
	overflow: hidden;
	position: relative;

	min-height: 200px;
}
.box .text {
	text-align: center;
}

.hover-handles .react-resizable-handle {
	display: none;
}
.hover-handles:hover .react-resizable-handle {
	display: block;
}
